.post-content{
    position: relative;
    padding-top: 50px;
    padding-bottom: 65px;
}

.post-content-container{
    width: 1335px;
    margin-left: auto;
    margin-right: auto;
}

.post-cover{
    padding-top: 305px;
}

.cover{
    position: absolute;
    width: 100%;
    height: 580px;
    top: 0;
    left: 0;
    overflow: hidden;
    background: #48478a no-repeat 50% 50%/cover;
}

.post-main-content{
    position: relative;
    width: 995px;
    height: 100%;
    margin-right: 25px;
}

/*  Article Headline  */

.post-headline{
    font-family: "Roboto-Condensed Light", "Arial", sans-serif;
    position: relative;
    background: #48478a;
    background: rgba(72,72,138,.9);
}

.post-headline:after{
    display: block;
    content: "";
    height: 0;
    overflow: hidden;
    clear: both;
}

.post-headline-content{
    box-sizing: border-box;
    max-width: 1200px;
    padding: 45px 95px 10px;
}

.headline-cover{
    min-height: 275px;
}

.post-headline-title{
    text-transform: uppercase;
    margin-bottom: 10px;
    color: #fff;
}

.post-headline-intro{
    display: block;
    font-size: 22px;
    color: #fff;
}

.post-headline-info{
    float: right;
    position: relative;
    font-size: 14px;
    line-height: 22px;
    padding: 19px 40px;
    color: #fff;
    color: rgba(255,255,255,.65);
}

.post-headline-info_date{
    float: left;
    margin-right: 20px;
}

/*  Article Body  */

.article-body{
    font-size: 16px;
    position: relative;
    background: #fff;
    padding: 45px 95px;
}

/*  Article Footer  */
.article-footer{
    display: flex;
    background: #fff;
    padding: 25px 95px;
    border-top: 1px solid #e5e5e5;
    margin-bottom: 10px;
}

.article-footer:after{
    display: block;
    content: "";
    height: 0;
    overflow: hidden;
    clear: both;
}

.article-footer-info{
    max-width: 400px;
    display: block;
    float: left;
    padding-left: 25px;
}

.article-footer-info_title{
    display: block;
    margin-bottom: 15px;
    margin-left: 80px;
    font-size: 14px;
    color: #999;
}

.article-footer_date{
    align-self: flex-end;
}